* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  margin: 0px;
}

.page {
  padding-bottom: 50px;
}

.document {
  img {
    display: block;
    max-width: 100%;
    max-height: 520px;
    font-size: 0;
    margin: 0px auto;
    padding: 0px;
  }

  blockquote {
    display: block;
    padding: 0px 30px;
    margin: 30px 0px;

    .quote {
      display: block;
      max-width: 680px;
      margin: 0px auto;
      text-align: center;
    }

    p.attribution {
      text-align: center;

      &:before {
        content: '–';
        padding-right: 5px;
      }
    }
  }

  .content {
    padding: 30px 20px;
    max-width: 720px;
    margin: 0px auto;

    p {
      line-height: 1.4em;
    }

    a {
      color: inherit;
    }

    li {
      margin: 1em 0;
      line-height: 1.4em;
    }

    time {
      display: block;
      color: #222;
      font-size: 0.9em;
      line-height: 1.4em;
    }

    code {
      font-family: Monaco, monospace;
    }

    pre {
      background-color: #eee;
      padding: 0.5em;
    }

    hr {
      border: none;
      border-top: 2px solid #dedede;
      margin: 2em 0;
    }
  }
}
